<template>
    <div class="container">
        <h2>tabs 选项卡组件</h2>
        <h3>选项卡组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>WiTabs属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>WiTabPane属性</h3>
        <PAttrTable :data="attrs2"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'tabList',
                    desc: 'tab列表标题',
                    type: 'Array',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'current',
                    desc: '当前选中的标签索引值，从0计数，开发者需要通过 onClick 事件来改变 current，从而切换 tab',
                    type: 'Number',
                    optional: '-',
                    default: '0'
                },
                {
                    param: 'tabDirection',
                    desc: 'Tab 方向，请跟 AtTabPane 保持一致',
                    type: 'String',
                    optional: 'horizontal,vertical',
                    default: 'horizontal'
                },
                {
                    param: 'height',
                    desc: 'Tab 高度，当 tabDirection=\'vertical\'时，需要设置',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'scroll',
                    desc: '是否滚动，当标签太多时，建议使用。否则会出现部分标签被隐藏',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                }
            ],
            attrs2: [
                {
                    param: 'current',
                    desc: '当前选中的标签索引值，从0计数，请跟 AtTabs 保持一致',
                    type: 'Number',
                    optional: '-',
                    default: '0'
                },
                {
                    param: 'tabDirection',
                    desc: 'Tab 方向，请跟 AtTabs 保持一致',
                    type: 'String',
                    optional: 'horizontal,vertical',
                    default: 'horizontal'
                },
                {
                    param: 'index',
                    desc: 'tabPane 排序，从0计数',
                    type: 'Number',
                    optional: '-',
                    default: '0'
                }
            ],
            events: [
                {
                    name: 'onClick',
                    desc: `点击或滑动时触发事件`,
                    param: `选中标签列表索引值`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiTabs,
        WiTabsPane,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                current:0
            }
        }

        tabClick(index){
            this.setState({
                current:index
            })
        }

        render () {
          const { current } =  this.state
          return (
              <View className='index'>
                  <WiTabs
                      scroll
                      className='wei'
                      tabDirection='horizontal'
                      tabList={['test1','test2','test3']}
                      onClick={this.tabClick.bind(this)}
                      current={current}
                      // renderTabBars={<View>5454</View>}
                  >
                      <WiTabsPane current={this.state.current} index={0}>
                          <View className='fixed-test'>第一个内容</View>
                      </WiTabsPane>
                      <WiTabsPane current={this.state.current} index={1}>
                          <View className='fixed-test'>第二个内容</View>
                      </WiTabsPane>
                      <WiTabsPane current={this.state.current} index={2}>
                          <View className='fixed-test'>第三个内容</View>
                      </WiTabsPane>
                  </WiTabs>
              </View>
          )
        }
}
<\/script>`
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    padding-bottom: 50px;
}
</style>
