<template>
    <div class="demo-block"
        @mouseover="onShowTip"
        @mouseout="onHideTip">
        <div class="meta" v-show="isShowCode">
            <pre>
                <code v-text="realCode"
                    class="language-html">
                </code>
            </pre>
        </div>
    </div>
</template>

<script>
import hljs from 'highlight.js'
import 'highlight.js/styles/googlecode.css'

export default {
    name: 'p-code',
    props: {
        code: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            isHovering: false,
            isShowCode: true
        }
    },
    computed: {
        realCode () {
            return this.code
        }
    },
    methods: {
        onShowTip () {
            this.isHovering = true
        },
        onHideTip () {
            this.isHovering = false
        },
        onSwitchCodeDisplay () {
            this.isShowCode = !this.isShowCode
        },
        highlightCode () {
            const preEl = document.querySelectorAll('pre code')

            preEl.forEach((el) => {
                hljs.highlightBlock(el)
            })
        }
    },
    // beforeCreate () {
    //     console.log(this)
    //     const children = this.$options._renderChildren
    //     for (let i = 0, l = children.length; i < l; i++) {
    //         const child = children[i]
    //         console.log(child)
    //     }
    // },
    mounted () {
        this.$nextTick(function () {
            this.highlightCode()
        })
    }
}
</script>

